var View = require('./view'),
    request = require('./request'),
    _template = require('./tpl/order.html');

var OrderDialog= View.extends({
    template: _template,
    events: {
        'touchstart .cancel': 'cancel',
        'touchstart .btn': 'order'
    },
    cancel: function(){
        this.$orderSuccess.hide();
        this.$order.hide();

    },

    order: function(){
        var me = this;
        var url = request.order.order  + me.urlEncode(this.model);
        if(me.$orderBtn.data('lock') == 'lock'){
            return ;
        }
        request.post(url)
            .done(function(res){
                if(res.retcode == '0'){
                    me.$order.hide();
                    me.$orderSuccess.show();
                    setTimeout(function(){
                        me.$orderSuccess.hide();
                    }, 2000);
                }else{

                }
            }).fail(function(){

            }).always(function(){
                me.$orderBtn.data('lock', '');
            });
    },
    urlEncode: function(param, key, encode) {
        if(param==null) return '';
        var paramStr = '';
        var t = typeof (param);
        if (t == 'string' || t == 'number' || t == 'boolean') {
            paramStr += '&' + key + '=' + ((encode==null||encode) ? encodeURIComponent(param) : param);
        } else {
            for (var i in param) {
                var k = key == null ? i : key + (param instanceof Array ? '[' + i + ']' : '.' + i);
                paramStr += this.urlEncode(param[i], k, encode);
            }
        }
        return paramStr;
    },
    init: function(){
        var me = this;
        me.render();
        me.$order = me.$('.js-order');
        me.$orderSuccess = me.$('.js-order-success');
        me.$orderBtn = me.$(".btn");
    },
    render: function(){
        var me = this;
        if(!!me.$order && me.$order.length > 0){
            return;
        }
        me.$el.html(me.template({}));
    },
    show: function(){
        this.$order.show();
    }
});

module.exports = OrderDialog;


