var event = require('./event');
var LoginDialog = require('./login'),
    OrderDialog = require('./order')
    request = require('./request');

require('./css/dialog.min.css');

$(function(){
    var loginDialog = new LoginDialog({
        el: '#loginDialog'
    });


    var orderDialog = new OrderDialog({
        el: '#orderDialog'
    });


    function showLogin(){
        loginDialog.show();
    }

    function showOrder(msisdn, pkgid, channelid, subsid, tid){
        orderDialog.model ={
            msisdn: msisdn || '',
            pkgid: pkgid|| '',
            channelid: channelid|| '',
            subsid: subsid|| '',
            tid: tid|| ''
        };
        orderDialog.show();
    }

    window.requestLogin = function(){
        var isLogin = !1, isOrder = !1;
        request.get(request.login.isLogin)
            .done(function(res){
                isLogin = res.islogin == 'true' ? 1 : 0;
            }).always(function(){
                if(!isLogin){
                    showLogin();
                }
            });
    };


    window.requestOrder = function(fn, msisdn, pkgid, channelid, subsid, tid){
        var  isLogin = !1,isOrder = !1;
        fn = typeof fn == 'function' ? fn : function(){};
        request.get(request.login.isLogin)
            .done(function(res){
                isLogin = res.islogin == 'true' ? 1 : 0;
            }).always(function(){
                console.log(isLogin);
                if(!isLogin){
                    showLogin();
                }else{
                    request.get(request.order.isOrder)
                        .done(function(res){
                            isOrder =res.retcode;
                        }).always(function(){
                            console.log(isOrder);
                            if(isOrder == '1'){
                                fn()
                            }else if(isOrder == '2'){
                                alert('鉴权失败')
                            }else if(isOrder == '0' ){
                                showOrder(msisdn, pkgid, channelid, subsid, tid);
                            }
                        });
                }
            });
        return false;
    };
});


